.admincontainer{
    @include contentContainer;
}
.logout{
    @include input-field;
    width: 200px;
    border-color: $dark-lime;
    font-size: 1.3em;
    float: right;
    background-color: white;
    &:hover{
        background-color: $light-lime;
    }
}

.dashboard{
    display: grid;
    grid-template-columns: [line1] 406px [line2] 406px;
    row-gap: 25px;
    column-gap: 24px;
    margin-top: 25px;
    &__messages{
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 2;
        width: 100%;
        background-color: white;
        border-top: 5px solid $dark-pink;
        height: 300px;
        overflow: scroll;
    }
    &__bookings{
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;
        width: 100%;
        background-color: white;
        border-top: 5px solid $dark-pink;
        height: 300px;
        overflow: scroll;
    }
    &__listings{
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 3;
        width: 100%;
        background-color: white;
        border-top: 5px solid $dark-pink;
        height: 625px;
        overflow: scroll;
    }
    &__submit{
        @include input-field;
        width: 100%;
        border-color: $dark-lime;
        font-size: 1.3em;
        float: left;
        background-color: white;
        &:hover{
            background-color: $light-lime;
        }
    }
    &__subtitle{
        margin-left: 20px;
        margin-top: 20px;
        font-size: 1.6em;      
    }
    &__titlebox{
        margin: 0;
        background-color: white;
        position: absolute;
        width: 406px;
        -webkit-box-shadow: 0px 4px 12px 0px rgba(0,0,0,0.09);
        -moz-box-shadow: 0px 4px 12px 0px rgba(0,0,0,0.09);
        box-shadow: 0px 4px 12px 0px rgba(0,0,0,0.09);
    }
    &__contentbox{
        margin-top: 90px;
    }
}

.errorMessage{
    @include input-warning;
}

.loginForm{
    &__inputfield{
        @include input-field;
        height: 45px;
        min-width: 400px;
        &--left{
            @include input-field;
            height: 45px;
            min-width: 400px;
        }
    }
    &__submit{
        @include input-field;
        width: 100%;
        border-color: $dark-lime;
        font-size: 1.3em;
        float: left;
        background-color: white;
        &:hover{
            background-color: $light-lime;
        }
    }
}

.listings{
    margin: 20px;
    &__line{
        width: 100%;
        height: 2px;
        background-color: $dark-lime;
        margin-top: 12px;
    }
    
}

.AddEstablishment{
    &__input{
        @include input-field;
        width: 100%;
        padding: 10px;
        margin-top: 5px;
        margin-bottom: 20px;
    }
    &___check{
        padding-top: 4px;
        padding-bottom: 4px;
        width: 100%;
        background-color: white;
        height: 47px;
    }
    &__submit{
        @include input-field;
        width: 100%;
        border-color: $dark-lime;
        font-size: 1.3em;
        padding: 7px;
        background-color: white;
        &:hover{
            background-color: $light-lime;
        }
        &:disabled {
            &:hover{
                background-color: white;
            }
        }
    }
    &__errorMessage{
        @include input-warning;
        margin-top: -18px;
        margin-bottom: 20px;
        &--hide{
            display: none;
        }
    }
}

@media only screen and (max-width: 800px) {
    .admincontainer{
        @include contentContainer;
        margin: 5%;
        padding: 5%;
        width: 90%;
    }

    .logout{
        @include input-field;
        width: 30%;
        border-color: $dark-lime;
        font-size: 1.3em;
        float: right;
        background-color: white;
        &:hover{
            background-color: $light-lime;
        }
    }
    
    .dashboard{
        display: grid;
        grid-template-columns: [line1] 100%;
        row-gap: 25px;
        column-gap: 24px;
        margin-top: 25px;
        &__messages{
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 1;
            grid-row-end: 2;
            height: 200px;
        }
        &__bookings{
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 2;
            grid-row-end: 3;
            height: 200px;

        }
        &__listings{
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 3;
            grid-row-end: 4;
            height: 400px;
        }
        
        &__subtitle{
            margin: 0 10px;
            margin-top: 20px;
            font-size: 1.2em;      
        }
        &__titlebox{
            margin: 0;
            background-color: white;
            position: absolute;
            width: 79%;
            -webkit-box-shadow: 0px 4px 12px 0px rgba(0,0,0,0.09);
            -moz-box-shadow: 0px 4px 12px 0px rgba(0,0,0,0.09);
            box-shadow: 0px 4px 12px 0px rgba(0,0,0,0.09);
        }
        &__contentbox{
            margin-top: 80px;
        }
    }
    
    .errorMessage{
        @include input-warning;
    }
    
    .loginForm{
        &__inputfield{
            min-width: 100%;
            &--left{
                min-width: 100%;
            }
        }
    }
    
    .listings{
        margin: 20px;
        &__line{
            width: 100%;
            height: 2px;
            background-color: $dark-lime;
            margin-top: 12px;
        }   
    }    
}